.v-sidebar-menu {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  display: flex;
  flex-direction: column;
  z-index: 999;
  box-sizing: border-box;
  width: 100%;
  text-align: left;
  transition: 0.3s max-width ease;
}
.v-sidebar-menu * {
  box-sizing: border-box;
}
.v-sidebar-menu .vsm--scroll-wrapper {
  height: 100%;
  overflow-y: auto;
  overflow-x: hidden;
}
.v-sidebar-menu .vsm--dropdown {
  padding: 5px;
}
.v-sidebar-menu .vsm--item {
  position: relative;
  display: block;
  width: 100%;
  white-space: nowrap;
}
.v-sidebar-menu .vsm--item_mobile {
  padding: 0 !important;
  margin: 0 !important;
}
.v-sidebar-menu .vsm--link {
  cursor: pointer;
  position: relative;
  display: flex;
  align-items: center;
  font-size: 16px;
  font-weight: 400;
  padding: 10px 15px;
  line-height: 30px;
  text-decoration: none;
  user-select: none;
  z-index: 20;
  transition: 0.3s all ease;
}
.v-sidebar-menu .vsm--link_active {
  font-weight: 600;
}
.v-sidebar-menu .vsm--link_disabled {
  opacity: 0.4;
  pointer-events: none;
}
.v-sidebar-menu .vsm--link_level-1 .vsm--icon {
  height: 35px;
  line-height: 35px;
  width: 35px;
  flex-shrink: 0;
  text-align: center;
  border-radius: 3px;
}
.v-sidebar-menu .vsm--link_mobile {
  margin: 0 !important;
  background-color: transparent !important;
  line-height: 35px !important;
}
.v-sidebar-menu .vsm--icon {
  display: inline-block;
  margin-right: 15px;
}
.v-sidebar-menu .vsm--title {
  flex-grow: 1;
}
.v-sidebar-menu .vsm--arrow {
  width: 30px;
  height: 30px;
  font-size: 14px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.v-sidebar-menu .vsm--arrow_default {
  position: relative;
  width: 12px;
  height: 12px;
  transition: transform 0.3s ease;
}
.v-sidebar-menu .vsm--arrow_default:before {
  content: "";
  width: 6px;
  height: 6px;
  border-left: 2px solid;
  border-bottom: 2px solid;
  transform: rotate(-135deg);
  display: inline-block;
  position: absolute;
  top: 2px;
  left: 0;
}
.v-sidebar-menu .vsm--arrow_open .vsm--arrow_default {
  transform: rotate(90deg);
}
.v-sidebar-menu .vsm--header {
  font-size: 14px;
  font-weight: 600;
  padding: 10px 15px;
  white-space: nowrap;
  text-transform: uppercase;
}
.v-sidebar-menu .vsm--badge_default {
  padding: 0px 6px;
  font-size: 12px;
  border-radius: 3px;
  height: 20px;
  line-height: 20px;
  font-weight: 600;
  text-transform: uppercase;
}
.v-sidebar-menu .vsm--toggle-btn {
  width: 100%;
  height: 65px;
  font-size: 18px;
  text-align: center;
  cursor: pointer;
  border: none;
  display: flex;
  justify-content: center;
  align-items: center;
}
.v-sidebar-menu .vsm--toggle-btn_default {
  position: relative;
  width: 12px;
  height: 12px;
}
.v-sidebar-menu .vsm--toggle-btn_default:before {
  content: "";
  display: inline-block;
  height: 12px;
  border-left: 2px solid;
  position: absolute;
  left: 0px;
  top: 0px;
}
.v-sidebar-menu .vsm--toggle-btn_default:after {
  content: "";
  display: inline-block;
  width: 6px;
  height: 6px;
  border-left: 2px solid;
  border-bottom: 2px solid;
  position: absolute;
  left: 4px;
  top: 2px;
  transform: rotate(45deg);
}
.v-sidebar-menu.vsm_collapsed .vsm--link_level-1.vsm--link_hover, .v-sidebar-menu.vsm_collapsed .vsm--link_level-1:hover {
  background-color: transparent !important;
}
.v-sidebar-menu.vsm_collapsed .vsm--toggle-btn_default {
  transform: scaleX(-1);
}
.v-sidebar-menu.vsm_collapsed.vsm_rtl .vsm--toggle-btn_default {
  transform: scaleX(1);
}
.v-sidebar-menu.vsm_rtl {
  right: 0;
  left: inherit;
  text-align: right;
  direction: rtl;
}
.v-sidebar-menu.vsm_rtl .vsm--icon {
  margin-left: 15px;
  margin-right: 0px;
}
.v-sidebar-menu.vsm_rtl .vsm--arrow_default {
  transform: scaleX(-1);
}
.v-sidebar-menu.vsm_rtl .vsm--arrow_open .vsm--arrow_default {
  transform: rotate(-90deg) scaleX(-1);
}
.v-sidebar-menu.vsm_rtl .vsm--toggle-btn_default {
  transform: scaleX(-1);
}
.v-sidebar-menu.vsm_relative {
  position: relative;
  height: 100%;
}
.v-sidebar-menu .expand-enter-active,
.v-sidebar-menu .expand-leave-active {
  transition: height 0.3s ease;
  overflow: hidden;
}
.v-sidebar-menu .expand-enter-from,
.v-sidebar-menu .expand-leave-to {
  height: 0 !important;
}
.v-sidebar-menu .slide-animation-enter-active {
  transition: width 0.3s ease;
}
.v-sidebar-menu .slide-animation-leave-active {
  transition: width 0.3s ease;
}
.v-sidebar-menu .slide-animation-enter-from,
.v-sidebar-menu .slide-animation-leave-to {
  width: 0 !important;
}
.v-sidebar-menu .fade-animation-enter-active {
  transition: opacity 0.3s ease, visibility 0.3s ease;
}
.v-sidebar-menu .fade-animation-leave-active {
  transition: opacity 0.3s ease, visibility 0.3s ease;
}
.v-sidebar-menu .fade-animation-enter-from,
.v-sidebar-menu .fade-animation-leave-to {
  opacity: 0 !important;
  visibility: hidden !important;
}

.v-sidebar-menu {
  background-color: #2a2a2e;
}
.v-sidebar-menu .vsm--link {
  color: #fff;
}
.v-sidebar-menu .vsm--link_level-1.vsm--link_active {
  box-shadow: 3px 0px 0px 0px #4285f4 inset;
}
.v-sidebar-menu .vsm--link_level-1 .vsm--icon {
  background-color: #1e1e21;
}
.v-sidebar-menu .vsm--link_hover {
  background-color: rgba(30, 30, 33, 0.5);
}
.v-sidebar-menu .vsm--link_mobile {
  color: #fff;
}
.v-sidebar-menu.vsm_expanded .vsm--link_level-1.vsm--link_open {
  color: #fff;
  background-color: #4285f4;
}
.v-sidebar-menu.vsm_expanded .vsm--link_level-1.vsm--link_open .vsm--icon {
  background-color: #4285f4;
}
.v-sidebar-menu.vsm_collapsed .vsm--link_level-1.vsm--link_hover .vsm--icon {
  color: #fff;
  background-color: #4285f4;
}
.v-sidebar-menu .vsm--dropdown {
  background-color: #36363b;
}
.v-sidebar-menu .vsm--mobile-bg {
  background-color: #4285f4;
}
.v-sidebar-menu.vsm_rtl .vsm--link_level-1.vsm--link_active {
  box-shadow: -3px 0px 0px 0px #4285f4 inset;
}
.v-sidebar-menu .vsm--header {
  color: rgba(255, 255, 255, 0.7);
}
.v-sidebar-menu .vsm--badge_default {
  color: #fff;
  background-color: #1e1e21;
}
.v-sidebar-menu .vsm--toggle-btn {
  color: #fff;
  background-color: #1e1e21;
}

